import React, { memo, useState, createContext } from "react";
import { Box, styled, FormGroup } from "@mui/material";
import HeaderForm from "../../Header/HeaderForm";
import InputName from "./Input/InputName";
import InputEmail from "./Input/InputEmail";
import InputMessage from "./Input/InputMessage";
import Send from "./Input/Send";
const MainFormGroup = styled(FormGroup)({
  width: "100%",
  height: "500px",
  display: "flex",
  flexDirection: "column",
  justifyContent: "space-around",
  alignItems: "center"
});
export const EmailData = createContext();
export const NameData = createContext();
export const SubmitData = createContext();
export const MessageData = createContext();
export const MainBox = styled(Box)({
  width: "100%",
  height: "20%",
  display: "flex",
  justifyContent: "center",
  alignItems: "center"
});
function Main() {
  const [text, setText] = useState("");
  const [email, setEmail] = useState("");
  const [name, setName] = useState("");
  const DATA = {
    name,
    email,
    text
  };
  return (
    <MainFormGroup>
      <MainBox>
        <HeaderForm></HeaderForm>
      </MainBox>
      <EmailData.Provider value={email}>
        <InputEmail
          onChange={(e) => {
            setEmail(e.target.value);
          }}
          empty={() => setName("")}
        />
      </EmailData.Provider>
      <NameData.Provider value={name}>
        <InputName
          onChange={(e) => {
            setName(e.target.value);
          }}
        />
      </NameData.Provider>
      <MessageData.Provider value={text}>
        <InputMessage
          onChange={(e) => {
            setText(e.target.value);
          }}
        />
      </MessageData.Provider>
      <SubmitData.Provider value={DATA}>
        <Send />
      </SubmitData.Provider>
    </MainFormGroup>
  );
}
export default memo(Main);
