import React, { memo, useContext, useState, useEffect, Fragment } from "react";
import { MainBox, SubmitData } from "../Main";
import Button from "@mui/material/Button";
import { Mail } from "../../../../About/Card/Heart/Firebase";
import {
  Dialog,
  DialogContent,
  DialogContentText,
  DialogActions
} from "@mui/material";
function Send() {
  const [color, setColor] = useState("primary");
  const [open, setOpen] = useState(false);
  const DATA = useContext(SubmitData);
  const CheckMail = (a) => {
    if (typeof a !== "undefined") {
      return a.indexOf("@gmail.com") > 2 && a[0] !== " " ? true : false;
    }
  };
  const CheckName = (a) => {
    if (typeof a !== "undefined")
      return a.length >= 3 && a.length <= 20 && a[0] !== " " ? true : false;
  };
  const Submit = (a, b, c) => {
    if (CheckMail(a) && CheckName(b) && c) {
      return true;
    }
    return false;
  };
  useEffect(() => {
    setColor("success");
  }, [Submit(DATA.email, DATA.name, DATA.text)]);
  return (
    <Fragment>
      <MainBox>
        <Button
          variant="outlined"
          onClick={() => {
            Mail.push(DATA);
            setOpen(true);
          }}
          color={color}
          disabled={Submit(DATA.email, DATA.name, DATA.text) ? false : true}
          sx={{ width: "80%" }}
        >
          Submit
        </Button>
      </MainBox>
      <Dialog open={open}>
        <DialogContent>
          <DialogContentText color={"success"}>
            Submit Successfully
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button
            autoFocus
            onClick={() => {
              setTimeout(() => {
                window.location.reload(false);
              }, 1500);
            }}
          >
            Ok
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
}
export default memo(Send);
