import React, { useState, memo, Fragment, useContext } from 'react'
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Alert from '@mui/material/Alert';
import Dialog from '@mui/material/Dialog';
import { CheckCircle } from "@mui/icons-material";
import Button from '@mui/material/Button';
import TextField from '@mui/material/TextField';
import Collapse from '@mui/material/Collapse';
import { MainBox, EmailData } from '../Main';

function InputEmail({ onChange }) {
    const email = useContext(EmailData);
    const [openEmail, setOpenEmail] = useState(false);
    const Valid = (a,b) => {
        var count = 0;
        for (let i = 0; i < b.length; i++) {
            if (a.indexOf(b[i]) === -1) {
                count+=1;
            };
        };
        if (count === b.length) return true;
        return false;
    };
    const chr = ['&',"$", "_", "-", "+", ",", "<", ">","'",'"', "?", "/", ":", ";", "{", "}", "|", "=", "~", "!", "#", "%"];
    const handleClickOpenEmail = () => {
        setOpenEmail(true);
    };
    const handleSubmitEmail = () => {
        setOpenEmail(false);
    };
    const Close = () => {
        setOpenEmail(false);
    };
    const CheckMail = (a) => {
        if (typeof (a) !== "undefined") {
            return ((a.indexOf("@gmail.com")) > 2 && (a[0] !== ' ') && (Valid(a, chr))) ? true : false;
        };
    };
    return (
        <Fragment>
            <MainBox>
                <Button variant="outlined"
                    color={CheckMail(email) ? "success" : "primary"}
                    size='large'
                    onClick={handleClickOpenEmail}
                    sx={{ width: "80%" }}>
                    {CheckMail(email) ? <CheckCircle /> : "Your Email"}
                </Button>
                <Dialog open={openEmail} onClose={Close}>
                    <DialogTitle>Email</DialogTitle>
                    <DialogContent dividers={true}>
                        <TextField
                            autoFocus
                            margin="dense"
                            id="email"
                            label="Your Email"
                            fullWidth
                            variant="standard"
                            autoComplete='off'
                            color='warning'
                            onChange={onChange}
                            value={openEmail ? email : ''}
                        />
                        <Collapse in={(!CheckMail(email) && (email.length >= 1)) ? true : false}>
                            <Alert
                                severity="error"
                                sx={{ mb: 2 }}>
                                {'Invalid Email'}
                            </Alert>
                        </Collapse>
                        <Collapse in={(CheckMail(email) && (email.length >= 1)) ? true : false}>
                            <Alert
                                severity="success"
                                sx={{ mb: 2 }}>
                                {'Valid Email'}
                            </Alert>
                        </Collapse>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={handleSubmitEmail} disabled={(CheckMail(email) && (email.length >= 1)) ? false : true}>Ok</Button>
                    </DialogActions>
                </Dialog>
            </MainBox>
        </Fragment>
    )
}
export default memo(InputEmail);
