import React from "react";
import styled from "styled-components";
import { BsReverseLayoutTextSidebarReverse as BsBar } from "react-icons/bs";
import Cube from "../../About/Card/Wrapper/Cube/Cube";
export const HeaderContainer = styled.header`
  width: 100%;
  height: 150px;
  text-align: center;
  top: 0;
  left: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: column;
`;
export default function Header() {
  return (
    <React.Fragment>
      <HeaderContainer>
        <div className="HeaderIcon">
          <BsBar />
        </div>
        <Cube />
        <div>
          <pre className="text" tabIndex="1">
            My Skills
          </pre>
        </div>
      </HeaderContainer>
    </React.Fragment>
  );
}
