import React, { memo, Fragment, useState, useContext } from "react";
import DialogTitle from "@mui/material/DialogTitle";
import Button from "@mui/material/Button";
import { MainBox, MessageData } from "../Main";
import DialogContent from "@mui/material/DialogContent";
import DialogActions from "@mui/material/DialogActions";
import Dialog from "@mui/material/Dialog";
import { CheckCircle } from "@mui/icons-material";
function InputMessage({ onChange }) {
  const text = useContext(MessageData);
  const [openText, setOpenText] = useState(false);
  const handleClickOpenText = () => {
    setOpenText(true);
  };
  const Close = () => {
    setOpenText(false);
  };
  const handleSubmitText = () => {
    setOpenText(false);
  };
  return (
    <Fragment>
      <MainBox>
        <Button
          variant="outlined"
          color={text.length >= 1 ? "success" : "primary"}
          size="large"
          onClick={handleClickOpenText}
          sx={{ width: "80%" }}
        >
          {text.length >= 1 ? <CheckCircle /> : "Message"}
        </Button>
        <Dialog open={openText} onClose={Close}>
          <DialogTitle>Message</DialogTitle>
          <DialogContent
            id="DialogContent"
            dividers={true}
            sx={{ width: 375, height: 300 }}
          >
            <textarea
              autoComplete="off"
              onChange={onChange}
              placeholder="Write your message"
              value={text}
            />
          </DialogContent>
          <DialogActions>
            <Button
              onClick={handleSubmitText}
              disabled={text.length >= 1 ? false : true}
            >
              Ok
            </Button>
          </DialogActions>
        </Dialog>
      </MainBox>
    </Fragment>
  );
}
export default memo(InputMessage);
