import React from 'react';
import Progress from './Progress';
export default function Container() {
  const ListLanguagesOne = [
    {
      Current: 95,
      Name: "HTML"
    },
    {
      Current: 80,
      Name: "CSS"
    },
    {
      Current: 40,
      Name: "JAVASCRIPT"
    },
    {
      Current: 45,
      Name: "PYTHON"
    }
  ];
  const ListLanguagesTwo = [
    {
      Current: 30,
      Name: "C++"
    },
    {
      Current: 20,
      Name: "C#"
    },
    {
      Current: 70,
      Name: "PASCAL"
    },
    {
      Current: 25,
      Name: "PHP"
    }
  ];
  return (
    <div className='Container'
      data-aos="flip-left"
      data-aos-easing="ease-out-cubic"
      data-aos-duration="550"
      data-aos-mirror="true"
      data-aos-once="false"
      data-aos-anchor-placement="center-bottom">
      <div className='Box-1'>
        {ListLanguagesOne.map(Language => {
          return <Progress name={Language.Name} key={Language.Current} progress={Language.Current} />
        })}
      </div>
      <div className='Box-2'>
        {ListLanguagesTwo.map(Language => {
          return <Progress name={Language.Name} key={Language.Current} progress={Language.Current} />
        })}
      </div>
    </div>
  );
};
