import React, { useState, memo, Fragment, useContext } from "react";
import DialogActions from "@mui/material/DialogActions";
import DialogContent from "@mui/material/DialogContent";
import DialogTitle from "@mui/material/DialogTitle";
import Alert from "@mui/material/Alert";
import Dialog from "@mui/material/Dialog";
import { CheckCircle } from "@mui/icons-material";
import Button from "@mui/material/Button";
import TextField from "@mui/material/TextField";
import Collapse from "@mui/material/Collapse";
import { MainBox, NameData } from "../Main";
function InputName({ onChange }) {
  const name = useContext(NameData);
  const [openName, setOpenName] = useState(false);
  const handleClickOpenName = () => {
    setOpenName(true);
  };
  const CheckName = (a) => {
    if (typeof a !== "undefined")
      return a.length >= 3 && a.length <= 20 && a[0] !== " " ? true : false;
  };
  const handleSubmitName = () => {
    setOpenName(false);
  };
  const Close = () => {
    setOpenName(false);
  };
  return (
    <Fragment>
      <MainBox>
        <Button
          variant="outlined"
          color={CheckName(name) ? "success" : "primary"}
          size="large"
          onClick={handleClickOpenName}
          sx={{ width: "80%" }}
        >
          {CheckName(name) ? <CheckCircle /> : "Your Name"}
        </Button>
        <Dialog open={openName} onClose={Close}>
          <DialogTitle>Name</DialogTitle>
          <DialogContent dividers={true}>
            <TextField
              autoFocus
              margin="dense"
              id="name"
              label="Your Name"
              fullWidth
              variant="standard"
              autoComplete="off"
              color="warning"
              onChange={onChange}
              value={name}
            />
            <Collapse in={!CheckName(name) && name.length >= 1 ? true : false}>
              <Alert severity="error" sx={{ mb: 2 }}>
                {"Invalid name"}
              </Alert>
            </Collapse>
            <Collapse in={CheckName(name) && name.length >= 1 ? true : false}>
              <Alert severity="success" sx={{ mb: 2 }}>
                {"Valid name"}
              </Alert>
            </Collapse>
          </DialogContent>
          <DialogActions>
            <Button
              onClick={handleSubmitName}
              disabled={CheckName(name) && name.length >= 1 ? false : true}
            >
              Ok
            </Button>
          </DialogActions>
        </Dialog>
      </MainBox>
    </Fragment>
  );
}
export default memo(InputName);
